import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';


import * as WebTorrent from 'src/scripts/webtorrent.min.js';
import * as Plyr from 'src/scripts/plyr.polyfilled.js';
declare var $ : any;

@Component({
  selector: 'app-mediaview',
  templateUrl: './mediaview.component.html',
  styleUrls: ['./mediaview.component.css']
})
export class MediaviewComponent implements OnInit {
  private mediacode:string;
  private seeding:string;

  private Peers:string|null;
  private Progress:string|null;
  private WEBRTC:string|null;

  private client:any|null
  private interval:any|null
  constructor(private route: ActivatedRoute) {
    if(this.client==null)
      this.client = new WebTorrent({});
    else
      console.log('current class')
   }


  ngOnInit() {
    if (!WebTorrent.WEBRTC_SUPPORT) {
      this.WEBRTC="DOESNOTSUPPORTED";
    }
    
    this.mediacode = this.route.snapshot.queryParams["mediaselected"];

    const player = new Plyr('#player', {
      /* options */
    });

    this.client.add('magnet:?xt=urn:btih:cd2fbd5df5ab832ff145f7edd7a22f355939a61d&dn=testvdo.mp4&tr=ws%3A%2F%2Ftracker.localhost',onTorrent);
  
    function onTorrent(torrent){
      console.log('Client is downloading:', torrent.infoHash)

      var file = torrent.files.find(function(file) {
        
        return file.name.endsWith('.mp4')
      });
      
      var container = document.getElementById('player');
      file.renderTo(container, {}, function callback() {
        console.log("ready to play!");
      });
    
      function updateSpeed () {    
        var progress = (100 * torrent.progress).toFixed(1)
        this.Peers =torrent.numPeers
        $( "#Peer" ).html( 'Peer: '+torrent.numPeers );
        $( "#Progress" ).html( 'Progress: '+progress+'% ' );
        $( "#Downloadspeed" ).html( 'Download speed: '+prettierBytes(torrent.downloadSpeed)+'/s' );
        $( "#Uploadspeed" ).html( 'Upload speed: '+prettierBytes(torrent.uploadSpeed)+'/s'  );
      }

      this.interval= setInterval(updateSpeed, 500);
      updateSpeed();

      function prettierBytes (num) {
        var exponent, unit, neg = num < 0, units = ['B', 'kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
        if (neg) num = -num
        if (num < 1) return (neg ? '-' : '') + num + ' B'
        exponent = Math.min(Math.floor(Math.log(num) / Math.log(1000)), units.length - 1)
        num = Number((num / Math.pow(1000, exponent)).toFixed(2))
        unit = units[exponent]
        return (neg ? '-' : '') + num + ' ' + unit
      }
    }

  }
  
  ngOnDestroy(){
    clearInterval(this.interval);
    this.client.destroy();

    
   
  }


}
