import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { contentlistviewmodel } from '../model/contentlistviewmodel';
import { Store } from '@ngrx/store';
import { CoreState, selectAuthState } from '../core/core.states';
import { get_anime_list_guest } from '../core/core.actions';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-anime',
  templateUrl: './anime.component.html',
  styleUrls: ['./anime.component.css']
})
export class AnimeComponent implements OnInit {
  private category:string|null
  private page:string|null
  private limit:number|null

  private anime_list:any|null
  private getStage: Observable<any>
  private contentlist:contentlistviewmodel[]

  constructor(private route: ActivatedRoute,private store: Store<CoreState>) {
    this.getStage = this.store.select(selectAuthState);
  }


  ngOnInit() {

    this.category = this.route.snapshot.queryParams["category"];
    this.page = this.route.snapshot.queryParams["page"];
    this.limit = this.route.snapshot.queryParams["limit"];
                                                //limit|page
    this.store.dispatch(new get_anime_list_guest(''))

    this.getStage.subscribe((state) => {
      
      if (state != undefined) {
        if (state.anime_list != null) {
          this.anime_list = state.anime_list
          
        }
      }
    })
    
  }
  ngOnDestroy() {

  }
}
