import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { AnimeComponent } from './anime/anime.component';
import { Routes, RouterModule } from '@angular/router';
import { StoreModule } from '@ngrx/store';
import { reducers } from './core/core.states';
import { EffectsModule } from '@ngrx/effects';
import { CoreEffects } from './core/core.effects';
import { AnimeviewComponent } from './animeview/animeview.component';
import { MediaviewComponent } from './mediaview/mediaview.component';
import { CreatetorrentComponent } from './createtorrent/createtorrent.component';
import { HttpClientModule } from '@angular/common/http';

const appRoutes : Routes = [
  {path:'',component:HomeComponent},
  {path:'home',component:HomeComponent},
  {path:'anime',component:AnimeComponent},

  {path:'animeview',component:AnimeviewComponent,
  children:[{
    path:'mediaview',component:MediaviewComponent
  }]
},
  {path:'mediaview',component:MediaviewComponent},

  {path:'createtorrent',component:CreatetorrentComponent},

]

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AnimeComponent,
    AnimeviewComponent,
    MediaviewComponent,
    CreatetorrentComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
  //  NgbModule.forRoot(),
    StoreModule.forRoot(reducers, {}),
    EffectsModule.forRoot([CoreEffects]),
    RouterModule.forRoot(appRoutes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
