import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({providedIn: 'root'})
export class CoreServices {
    private BASE_URL = 'http://api.localhost/';
    constructor(private http: HttpClient) { }

    get_anime_list_guest(payload:any){
        const url = this.BASE_URL+"dashboard/anime_list_guest"
        let header = new HttpHeaders().set('Content-Type','application/json')
        let params = {'limit':payload.limit,'page':payload.page,'category':payload.category}
        return this.http.get(url,{headers:header,params:params})
    }
    find_anime_by_id(payload:any){
        const url = this.BASE_URL+"dashboard/find_anime_list_guest"
        let header = new HttpHeaders().set('Content-Type','application/json')
        let params = {'_id':payload._id}
        return this.http.get(url,{headers:header,params:params})
    }

}