import { Component, OnInit } from '@angular/core';
import * as WebTorrent from 'src/scripts/webtorrent.min.js';

declare var $: any;

@Component({
  selector: 'app-createtorrent',
  templateUrl: './createtorrent.component.html',
  styleUrls: ['./createtorrent.component.css']
})
export class CreatetorrentComponent implements OnInit {
  constructor() { }

  ngOnInit() {


  }

  postMethod(files: FileList) {
 
    var client = new WebTorrent();
    
    client.seed(files, { announceList: [["ws://tracker.localhost"]]}, function (torrent) {
      console.log('Client is seeding ' + torrent.magnetURI);
      $( "#magnetURI" ).html( torrent.magnetURI );
      
    })

  }

}
