import { Action } from '@ngrx/store';
export enum CoreActionTypes {
    GET_ANIME_LIST_GUEST = "[Auth] Get Anime List Guest",
    GET_ANIME_LIST_GUEST_SUCCEED = "[Auth] Get Anime List Guest Succeed",
    FIND_ANIME_BY_ID ="[Auth] Find Anime By Id",
    FIND_ANIME_BY_ID_SUCCEED ="[Auth] Find Anime By Id Succeed"
}

export class get_anime_list_guest implements Action {
    readonly type = CoreActionTypes.GET_ANIME_LIST_GUEST;
    constructor(public payload: any) { }
  }

  export class get_anime_list_guest_succeed implements Action {
    readonly type = CoreActionTypes.GET_ANIME_LIST_GUEST_SUCCEED;
    constructor(public payload: any) { }
  }

  export class find_anime_by_id implements Action {
    readonly type = CoreActionTypes.FIND_ANIME_BY_ID;
    constructor(public payload: any) { }
  }

  export class find_anime_by_id_succeed implements Action {
    readonly type = CoreActionTypes.FIND_ANIME_BY_ID_SUCCEED;
    constructor(public payload: any) { }
  }


export type All = 
get_anime_list_guest
|get_anime_list_guest_succeed
|find_anime_by_id
|find_anime_by_id_succeed
;

