import { CoreActionTypes, All } from './core.actions';
export interface State {
    anime_list:any|null
    anime_list_view:any|null
}
export const initialState: State = {
    anime_list:null,
    anime_list_view:null
}

export function reducer(state = initialState, action: All) {
    switch(action.type){

        case CoreActionTypes.GET_ANIME_LIST_GUEST_SUCCEED:{
            return {
                ...state,
                anime_list:action.payload
            }
        }
        case CoreActionTypes.FIND_ANIME_BY_ID_SUCCEED:{
            console.log(action.payload)
            return {
                ...state,
                anime_list_view:action.payload
            }
        }
        default:{
            return {...state}
        }
    }
}