import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { animeviewmodel } from '../model/animeviewmodel';
import { Store } from '@ngrx/store';
import { CoreState, selectAuthState } from '../core/core.states';
import { Observable } from 'rxjs';
import { get_anime_list_guest_succeed, get_anime_list_guest, find_anime_by_id_succeed, find_anime_by_id } from '../core/core.actions';
import { count } from 'rxjs/operators';

@Component({
  selector: 'app-animeview',
  templateUrl: './animeview.component.html',
  styleUrls: ['./animeview.component.css']
})
export class AnimeviewComponent implements OnInit {
  private animecode: string;
  private animeview: animeviewmodel;
  private getStage: Observable<any>
  private anime_list: any | null
  private anime_list_view: any | null
  constructor(private route: ActivatedRoute, private store: Store<CoreState>) {
    this.getStage = this.store.select(selectAuthState);
  }

  ngOnInit() {
    this.animecode = this.route.snapshot.queryParams["animeselected"];
    this.getStage.subscribe((state) => {
      if (state != undefined) {
        if (state.anime_list != null) {
          this.anime_list = state.anime_list
          for (var i = 0, len = this.anime_list.anime_list.length; i < len; i++) {
            if (this.anime_list.anime_list[i]._id == this.animecode) {
              this.anime_list_view = this.anime_list.anime_list[i]
              break
            }
          }
        }
      }
    })


    if(this.anime_list_view==undefined){
      const payload ={_id:this.animecode}
      this.store.dispatch(new find_anime_by_id(payload))
      this.getStage.subscribe((state) => {
        if (state != undefined) {
          if(state.anime_list_view!=null){
            this.anime_list_view=state.anime_list_view
          }
        }
      })
    }

    console.log(this.anime_list_view)






    this.animeview = {
      img_url: "https://www.anime-sugoi.com/upload/9b2489b6e31b018efe22224f88403266.jpg",
      story: "เกิดใหม่ทั้งทีก็เป็นสไลม์ไปซะแล้ว มิคามิ ซาโตรุ หนุ่มโสด ไม่เคยมีแฟน วัย 37 ปี ถูกแทงตายเพราะช่วยเพื่อนจากคนร้าย เขาได้เกิดใหม่ในต่างโลกด้วยร่างของสไลม์ ได้พบมังกร เวรุโดร่า ผู้ถูกผนึกมาร่วม 3 ร้อยปีและรู้ว่าเขามาจากต่างโลก ด้วยความเบื่อหน่ายในชีวิตจึงยอมเป็นเพื่อนกับสไลม์ ตั้งชื่อให้เขา “ริมุรุ” และให้เขากลืนตนเข้าไปในร่าง ด้วย2 ความสามารถ “นักล่า” ที่ทำให้เขาสามารถชิงความสามารถของผู้ถูกกลืน และ “ปราชญ์ผู้ยิ่งใหญ่” ทำให้เขาเข้าใจเรื่องราวในโลกใหม่ ส่งผลให้เขาพัฒนาเป็นสไลม์ที่น่าเกรงขามต่อเหล่ามอนสเตอร์ทั่วไป จนกลายเป็นปีศาจที่ยิ่งใหญ่ในภายหลัง",
      tag: "Tensei shitara Slime Datta Ken;เกิดใหม่ทั้งทีก็เป็นสไลม์ไปซะแล้ว;Fantasy;Shounen;ซับไทย;ยังไม่จบ;",
      fansub: " [Otaku-fs&Tartarus];[Nemuria-FS]",
      animeepisode: [
        { anime_name: "Tensei shitara Slime Datta Ken ตอนที่ 1 ซับไทย", anime_media_link: "a1" },
        { anime_name: "Tensei shitara Slime Datta Ken ตอนที่ 2 ซับไทย", anime_media_link: "a2" },
        { anime_name: "Tensei shitara Slime Datta Ken ตอนที่ 3 ซับไทย", anime_media_link: "a3" },
      ]
    }

  }

}
