
import { Injectable } from '@angular/core';

import { Actions, Effect, ofType } from '@ngrx/effects';
import { Observable,of } from 'rxjs';
import { map, switchMap, catchError, tap } from 'rxjs/operators';
import { Store } from '@ngrx/store';

import { CoreState} from './core.states';
import { CoreServices } from './core.services';
import { CoreActionTypes, get_anime_list_guest, get_anime_list_guest_succeed, find_anime_by_id, find_anime_by_id_succeed } from './core.actions';
import { Router } from '@angular/router';
declare var $ : any;

@Injectable()
export class CoreEffects {
  constructor(
    private router: Router,
    private actions: Actions,
    private coreService: CoreServices,
    private store: Store<CoreState>
  ) { }

  @Effect({ dispatch: false })
  get_anime_list_guest:Observable<any>=this.actions.pipe(
    ofType(CoreActionTypes.GET_ANIME_LIST_GUEST),
    map((action:get_anime_list_guest)=>action.payload),
    switchMap(payload=>{
      return this.coreService.get_anime_list_guest(payload).pipe(
        map(resp=>{
          this.store.dispatch(new get_anime_list_guest_succeed(resp))
        }),
        catchError(err=>{
          return of()
        })
      )
    })
  )

  @Effect({ dispatch: false })
  find_anime_by_id:Observable<any>=this.actions.pipe(
    ofType(CoreActionTypes.FIND_ANIME_BY_ID),
    map((action:find_anime_by_id)=>action.payload),
    switchMap(payload=>{
      return this.coreService.find_anime_by_id(payload).pipe(
        map(resp=>{
          this.store.dispatch(new find_anime_by_id_succeed(resp))
        }),
        catchError(err=>{
          return of()
        })
      )
    })
  )
}